@font-face {
    font-family: Roboto Mono;
    src: url(/static/RobotoMono-Light.ttf);
  }

  @font-face {
    font-family: Russo One;
    src: url(/static/RussoOne-Regular.ttf);
  } 

html {
    scroll-behavior: smooth;
    
    
}


body::-webkit-scrollbar {
    display: none;}


body {
    font-family: Roboto Mono;
    background-color: black;;
    
}   
    




canvas
{
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    animation: introduccion 6s forwards 2s

    
}



main {
    position: absolute;
    color: white;  
    
}


header {
   color: azure;
   text-align: center;
   padding-top: 20em;
   padding-bottom:30em;

   font-family: Russo One;
   
}

h1 {
    
    text-align: center;
    size: 3em;
    animation-name: titulo;
    animation-duration: 4s;
    
}

h2 {
    font-family: Russo One;
    
}

blockquote {
    opacity: 0;
}

section {
opacity: 1;
}

.seccion1 {
   
    margin-bottom: 20em;
    margin-top: 15em;
    
}

.rainbow-box {
    border: 5px solid transparent;
    border-image: linear-gradient(to bottom right, #b827fc 0%, #2c90fc 25%, #b8fd33 50%, #fec837 75%, #fd1892 100%);
    border-image-slice: 1;
    padding-left: 1em;
    padding-right: 1em;
    padding-top: 2em;
    background-color: rgba(0,0,0,.5);;
    
    

  }

  button {
      margin-bottom: 1em;
  }

#empezar {
    opacity: 0;
        color: #ffffff;
        animation: introduccion 6s forwards 2s
 
        
        
    
}

.textoCentrado {
    text-align: center;
    
}

.relleno {
    padding-bottom: 4em;
}


#segundaParada {
    margin-bottom: 10em;
}

#terceraParada {
    margin-bottom: 15em;
}
#cuartaParada {
    margin-bottom: 15em;
    margin-top: 30em;
}

a:hover {
    color: rgb(255, 97, 234);
    text-decoration: none;

}

a {
    color: rgb(255, 97, 234);
    text-decoration: none;
    
}

.centrales {
    margin-top: 5em;
}


/* Animaciones */

@keyframes titulo {
   from {opacity: 0}
   to {opacity: 1;} 
}

@keyframes introduccion {
    from {opacity: 0}
   to {opacity: 1;}
}


@keyframes arcoiris {
    0% {color: white;}
    16% {color: red;}
    32% {color: orange}
    48% {color: yellow}
    64% {color: green;}
    80% {color: blue}
    100% {color: indigo}
}

/*Responsive movil*/

@media only screen and (max-width: 768px){
    header {
        padding-top: 10em;
    }

   
}

@media only screen and (min-width: 768px){
    #primerBoton {
        display: none;
    }

    #segundoBoton {
        display: none;
    }

    #tercerBoton {
        display: none;
    }
}